.op-menu
  display: flex
  flex-direction: column

  &--item
    white-space: nowrap

    &-title
      font-weight: 400
      overflow: hidden
      text-overflow: ellipsis

      > .spot-icon
        width: 1.25rem
        height: 1.25rem
        margin-left: 0.25rem
        margin-right: 0
        color: var(--primary-color--minor1)


    &-action
      display: block
      height: 32px
      line-height: 32px
      text-decoration: none
      padding: 0px 13px 0px 10px
      font-weight: 400
      color: var(--context-menu-unselected-font-color)

      .spot-icon
        width: 1.25rem
        height: 1.25rem

        &:first-child
          margin-right: 0.75rem

      &:focus
        background-color: #f0f0f0
        color: var(--context-menu-unselected-font-color)

      &:hover,
      &:active
        background-color: #f0f0f0
        color: var(--context-menu-hover-font-color)
        cursor: pointer
        text-decoration: none

  &--item-action,
  &--headline
    padding: 0 10px

  &--headline
    font-size: 12px
    color: var(--gray-dark)
    text-transform: uppercase

  &--separator
    border-bottom: 1px solid var(--content-form-separator-color)
    margin: 0 0 10px
    background: none
